.profile__add-button {
  background: url(../../../images/Add_Button.svg);
  background-repeat: no-repeat;
  width: 150px;
  height: 50px;
  padding: 0;
  border: 0;
  margin-left: auto;
  margin-top: 35px;
  margin-bottom: 35px;
  cursor: pointer;
}

.profile__add-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 671px) {
  .profile__add-button {
    background: url(../../../images/Rectangle.svg);
    width: 282px;
    margin: auto;
    background-repeat: no-repeat;
    background-size: 282px;
  }
}
