.profile__subtitle {
  color: #ffffff;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  margin-top: 16px;
  margin-bottom: 25px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 767px) {
  .profile__subtitle {
    font-size: 14px;
    line-height: 17px;
    margin: 14px auto 36px;
  }
}
