.profile {
  max-width: 880px;
  display: flex;
  margin: 36px auto 51px;
}

@media screen and (max-width: 901px) {
  .profile {
    max-width: 650px;
  }
}

@media screen and (max-width: 670px) {
  .profile {
    flex-direction: column;
    align-items: center;
    margin-bottom: 36px;
  }
}
